.most-popular-destinations,
.plan-your-next {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.most-popular-destinations {
  font-size: 30px;
  text-transform: capitalize;
  color: #303030;
}
.title-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 6px;
}
.view-all-destinations {
  position: relative;
  font-size: 18px;
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: Roboto;
  color: #1262ae;
  text-align: right;
  display: inline-block;
  width: 197.33px;
  flex-shrink: 0;
}
.arrow-right-icon {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.cards-container,
.destinations-titles,
.view-all-top {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.view-all-top {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: #fff;
  border-radius: 31px;
  width: 231.38px;
  flex-shrink: 0;
  align-items: flex-start;
  gap: 10px;
}
.cards-container,
.destinations-titles {
  align-self: stretch;
  align-items: center;
}
.cards-container {
  align-items: flex-start;
  margin-left: -16px;
  margin-right: -16px;
}
.view-all-destinations1 {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 500;
  display: inline-block;
  width: 197.33px;
  flex-shrink: 0;
}
.pop-destinations-main,
.view-all-bottom {
  align-items: flex-start;
  justify-content: flex-start;
}
.view-all-bottom {
  border-radius: 31px;
  background-color: #fff;
  width: 231.38px;
  display: none;
  flex-direction: row;
  gap: 10px;
  text-align: right;
}
.pop-destinations-main {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  gap: 24px;
  text-align: left;
  font-size: 18px;
  color: #1262ae;
  font-family: Roboto;
}
@media screen and (max-width: 767px) {
  .view-all-top {
    display: none;
  }
  .cards-container {
    flex-wrap: wrap;
    margin: -16px;
  }
  .view-all-destinations1 {
    width: auto;
  }
  .view-all-bottom {
    display: flex;
    margin-top: 16px;
  }
}
@media screen and (max-width: 427px) {
  .most-popular-destinations {
    font-size: 22px;
  }
  .title-container {
    flex: unset;
    align-self: stretch;
  }
  .destinations-titles {
    flex-direction: column;
    align-self: flex-start;
  }
}
