}
.to-and-from {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.horizontal-line {
  align-self: stretch;
  position: relative;
  border-top: 1px solid #eaeaea;
  box-sizing: border-box;
  height: 1px;
  flex-shrink: 0;
}
.days-to-go-container,
.departs-on-1 {
  position: relative;
  letter-spacing: 0.02em;
  display: inline-block;
  flex-shrink: 0;
}
.departs-on-1 {
  text-transform: capitalize;
  width: 203.9px;
}
.days-to-go-container {
  text-align: right;
  width: 87.23px;
}
.departure-details {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  color: #8e8e94;
}
.flight-main-container {
  border-radius: 8px;
  border: 1px solid #eaeaea;
  display: flex;
  flex-direction: column;
  padding: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 21px;
}
.line-separator {
  position: relative;
  border-right: 1px solid #eaeaea;
  box-sizing: border-box;
  width: 1px;
  height: 145.99px;
  flex-shrink: 0;
}
.prepare-for-your {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.hotel-icon {
