.trip-menus {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.flight-details,
.prepare-menu,
.upcoming-flight-section {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.prepare-menu {
  flex: 1;
  flex-direction: column;
  gap: 20px;
  font-size: 18px;
  color: #cecece;
}
.flight-details,
.upcoming-flight-section {
  align-self: stretch;
  flex-direction: row;
  gap: 36px;
  font-size: 24px;
  color: #1262ae;
}
.upcoming-flight-section {
  flex-direction: column;
  gap: 20px;
  text-align: left;
  font-size: 18px;
  color: #303030;
  font-family: Roboto;
}
@media screen and (max-width: 1440px) {
  .flight-main-container {
    width: 520px;
  }
}
@media screen and (max-width: 767px) {
  .horizontal-line {
    width: 100%;
  }
  .flight-main-container {
    width: 100%;
    display: block;
    margin-bottom: 30px;
  }
  .line-separator {
    display: none;
  }
  .prepare-menu {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .flight-details {
    flex-direction: column;
  }
  .upcoming-flight-section {
    display: block;
  }
}
@media screen and (max-width: 427px) {
  .hotel {
    padding-bottom: 20px;
    box-sizing: border-box;
    width: 33.33%;
    text-align: center;
  }
  .trip-menus {
    flex-direction: row;
    text-align: center;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: -28px;
    margin-right: -28px;
  }
}
