.sia-icon {
  position: relative;
  width: 34.77px;
  height: 46px;
  flex-shrink: 0;
}
.singapore-airlines1 {
  position: relative;
  letter-spacing: 0.26px;
  display: inline-block;
  width: 140px;
  flex-shrink: 0;
}
.airline-section1 {
  width: 20%;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 7px;
}
.am2,
.sin3 {
  position: absolute;
  left: 0;
  display: inline-block;
}
.am2 {
  width: 100%;
  top: 0;
  letter-spacing: 0.32px;
  font-weight: 500;
}
.sin3 {
  width: 79.51%;
  top: 63.46%;
  font-size: 16px;
  letter-spacing: 0.26px;
  color: #646468;
}
.departure-details2 {
  position: relative;
  width: 93px;
  height: 52px;
  flex-shrink: 0;
}
.flighticon1 {
  position: absolute;
  height: 27.46%;
  width: 83.03%;
  top: 0;
  right: 8.26%;
  bottom: 72.54%;
  left: 8.71%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.h-55m-non-stop1 {
  position: absolute;
  top: 65.45%;
  left: 0;
  letter-spacing: 0.21px;
}
.flightduration1 {
  position: relative;
  width: 129px;
  height: 55px;
  flex-shrink: 0;
  text-align: center;
  font-size: 16px;
  color: #8e8e94;
}
.sin4 {
  position: absolute;
  width: 81.25%;
  top: 63.46%;
  left: 17.65%;
  font-size: 16px;
  letter-spacing: 0.26px;
  color: #646468;
  display: inline-block;
}
.arrival-details1 {
  position: relative;
  width: 91px;
  height: 52px;
  flex-shrink: 0;
  text-align: right;
}
.flight-details-section1 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 64%;
  text-align: left;
  font-size: 21px;
  color: #1262af;
}
.price-section-item {
  position: relative;
  width: 1px;
  height: 100px;
  flex-shrink: 0;
}
.s-7301 {
  flex: 1;
  position: relative;
  letter-spacing: 0.34px;
}
.flight-card1,
.price-section1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.price-section1 {
  width: 128px;
  flex-shrink: 0;
  font-size: 24px;
  color: #fba403;
}
.flight-card1 {
  align-self: stretch;
  border-radius: 6px;
  background-color: #fff;
  border: 0.7px solid #cecece;
  padding: 0 25px;
  gap: 50px;
  text-align: center;
  font-size: 16px;
  color: #57575d;
  font-family: Roboto;
}
@media screen and (max-width: 767px) {
  .airline-section1 {
    flex-direction: column;
  }
  .flight-details-section1 {
    flex-direction: row;
  }
}
@media screen and (max-width: 427px) {
  .sia-icon {
    transform: scale(0.8);
  }
  .airline-section1 {
    flex-direction: row;
    width: 100% !important;
    margin-left: 40px;
  }
  .flightduration1 {
    transform: scale(0.9);
  }
  .flight-details-section1 {
    width: 90%;
    flex-direction: row;
    padding: 0;
    box-sizing: border-box;
    margin-right: 0 !important;
    flex: unset;
    align-self: stretch;
  }
  .price-section-item {
    display: none;
  }
  .flight-card1 {
    flex-direction: column;
    padding: 20px 0;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .am2 {
    font-size: 16px;
  }
  .departure-details2 {
    width: 80px;
  }
  .h-55m-non-stop1 {
    font-size: 13px;
  }
  .arrival-details1 {
    width: 80px;
  }
}
@media screen and (max-width: 310px) {
  .airline-section1 {
    flex-direction: row;
    padding-bottom: 10px;
    box-sizing: border-box;
    width: 100%;
    margin-right: 0;
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 16px;
  }
  .flight-details-section1 {
    width: 100%;
    margin-right: 0;
    flex: unset;
    align-self: stretch;
  }
  .price-section-item {
    display: none;
  }
  .flight-card1,
  .price-section1 {
    box-sizing: border-box;
    width: 100%;
  }
  .price-section1 {
    padding: 6px;
    background-color: #f5f7f8;
    margin-top: 16px;
  }
  .flight-card1 {
    display: flex;
    flex-direction: column;
    padding: 25px;
  }
}
