.search-flights3 {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 81.5px);
  letter-spacing: 0.04em;
  font-weight: 500;
}
.primary-button {
  position: relative;
  border-radius: 6px;
  background-color: #1262af;
  width: 220px;
  height: 72px;
  flex-shrink: 0;
  font-size: 18px;
  color: #fff;
}
.flight-cards {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 9px;
  text-align: center;
  font-size: 16px;
  color: #57575d;
  font-family: Roboto;
}
@media screen and (max-width: 310px) {
  .flight-cards {
    width: 100%;
  }
}
