.matterhorn-suites-image {
  align-self: stretch;
  position: relative;
  border-radius: 10px;
  max-width: 100%;
  overflow: hidden;
  height: 200px;
  flex-shrink: 0;
  object-fit: cover;
}
.entire-bungalow {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.02em;
  color: #8e8e94;
}
.matterhorn-suites,
.night {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
}
.matterhorn-suites {
  font-size: 22px;
  text-transform: capitalize;
}
.night {
  font-size: 18px;
}
.stay-details-rows {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 6px;
}
.video-icon {
  position: relative;
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  overflow: hidden;
  cursor: pointer;
}
.stay-details {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 11px;
}
.div2,
.reviews {
  position: absolute;
  top: 0;
  letter-spacing: 0.04em;
}
.reviews {
  left: 53.63px;
  display: inline-block;
  width: 216.37px;
}
.div2 {
  left: 20.34px;
  font-weight: 500;
}
.parent,
.vector-icon {
  position: absolute;
  top: 0;
  left: 0;
}
.vector-icon {
  height: 90.47%;
  width: 37.54%;
  right: 62.46%;
  bottom: 9.53%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.parent {
  width: 44.34px;
  height: 19px;
  color: #303030;
}
.button1,
.rating {
  position: relative;
  color: #1262af;
}
.rating {
  align-self: stretch;
  height: 19px;
  flex-shrink: 0;
}
.button1 {
  font-size: 15px;
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: Roboto;
  text-align: left;
}
.hotel-card,
.hotel-card-1,
.more-details-button {
  align-self: stretch;
  display: flex;
  box-sizing: border-box;
}
.more-details-button {
  cursor: pointer;
  border: 1px solid #ccdcec;
  padding: 0;
  background-color: transparent;
  border-radius: 4px;
  height: 46px;
  flex-shrink: 0;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.hotel-card,
.hotel-card-1 {
  flex: 1;
  align-items: flex-start;
  width: 25%;
}
.hotel-card {
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  flex-direction: column;
  padding: 15px 13px;
  justify-content: flex-start;
  gap: 23px;
}
.hotel-card:hover {
  background-color: rgba(226, 226, 226, 0.1);
  cursor: pointer;
}
.hotel-card-1 {
  flex-direction: row;
  padding: 0 12px;
  justify-content: center;
  text-align: left;
  font-size: 16px;
  color: #303030;
  font-family: Roboto;
}
@media screen and (max-width: 1440px) {
  .hotel-card {
    width: 25% !important;
  }
}
@media screen and (max-width: 767px) {
  .hotel-card {
    width: 100%;
  }
  .hotel-card-1 {
    width: 33.33%;
  }
}
@media screen and (max-width: 427px) {
  .matterhorn-suites {
    font-size: 20px;
  }
  .hotel-card,
  .more-details-button {
    width: 100% !important;
  }
  .hotel-card {
    margin-bottom: 20px;
  }
  .hotel-card-1 {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
}
