.results-image-icon {
  position: relative;
  border-radius: 10px 0 0 10px;
  width: 260px;
  height: 220px;
  flex-shrink: 0;
  object-fit: cover;
}
.king-bed-standard {
  position: relative;
  letter-spacing: 0.02em;
  display: inline-block;
  width: 146.92px;
}
.div3,
.holiday-inn-expre,
.reviews1 {
  letter-spacing: 0.04em;
  display: inline-block;
}
.holiday-inn-expre {
  position: relative;
  font-size: 22px;
  text-transform: capitalize;
  color: #303030;
  width: 232.87px;
}
.div3,
.reviews1 {
  position: absolute;
  top: 0;
  left: 55.97px;
  width: 117.94px;
}
.div3 {
  left: 21.99px;
  font-weight: 500;
  width: 23.99px;
}
.stars,
.vector-icon1 {
  position: absolute;
  top: 0;
  left: 0;
}
.vector-icon1 {
  height: 84.21%;
  width: 34.78%;
  right: 65.22%;
  bottom: 15.79%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.stars {
  width: 45.98px;
  height: 19px;
  color: #303030;
}
.review {
  position: relative;
  width: 173.91px;
  height: 19px;
  flex-shrink: 0;
  color: #1262af;
}
.result-and-video {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 6px;
}
.video-icon1 {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.results-title {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.night1,
.s-286 {
  position: absolute;
  letter-spacing: 0.02em;
}
.s-286 {
  top: 0;
  left: 0;
  text-transform: capitalize;
}
.night1 {
  top: 8px;
  left: 73px;
  font-size: 14px;
}
.pricing,
.view-details-button-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 112px;
  height: 26px;
}
.view-details-button-child {
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  border-radius: 6px;
  background-color: #1262af;
}
.search-flights1 {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 54.5px);
  font-size: 18px;
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: Roboto;
  color: #fff;
  text-align: center;
}
.view-details-button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  top: 37.58px;
  left: 0.49px;
  width: 164px;
  height: 46px;
}
.price-and-cta {
  position: relative;
  width: 164.49px;
  height: 83.58px;
  flex-shrink: 0;
  font-size: 22px;
  color: #303030;
}
.results-card,
.results-details {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.results-details {
  flex: 1;
  flex-direction: column;
  padding: 15px 12px 15px 0;
  gap: 31px;
}
.results-card {
  align-self: stretch;
  border-radius: 10px;
  background-color: rgba(18, 98, 175, 0.05);
  flex-direction: row;
  gap: 16px;
  text-align: left;
  font-size: 16px;
  color: #8e8e94;
  font-family: Roboto;
}
@media screen and (max-width: 427px) {
  .results-image-icon {
    width: 100% !important;
    border-radius: 10px !important;
  }
  .results-details {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    flex: unset;
    align-self: stretch;
  }
  .results-card {
    flex-direction: column;
  }
}
