.unsplash5mv818tzxeo-icon {
  align-self: stretch;
  position: relative;
  border-radius: 10px 10px 0 0;
  max-width: 100%;
  overflow: hidden;
  height: 242px;
  flex-shrink: 0;
  object-fit: cover;
}
.bali,
.d3n {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.02em;
  font-family: Roboto;
  text-align: left;
}
.bali {
  font-size: 20px;
  font-weight: 500;
  color: #424242;
}
.d3n {
  font-size: 16px;
  color: #999;
}
.bali-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 3px;
}
.div1 {
  position: relative;
  font-size: 32px;
  line-height: 34px;
  font-family: "Baloo Bhai";
  color: #1262af;
  text-align: right;
}
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.holiday-details,
.rec-card-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.holiday-details {
  align-self: stretch;
  border-radius: 0 0 10px 10px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  padding: 20px 16px;
  justify-content: center;
}
.rec-card-1 {
  cursor: pointer;
  border: 0;
  padding: 0 16px;
  background-color: transparent;
  flex: 1;
  box-sizing: border-box;
  justify-content: flex-start;
  width: 25%;
}
@media screen and (max-width: 767px) {
  .rec-card-1 {
    width: 50%;
    margin-bottom: 32px;
  }
}
@media screen and (max-width: 427px) {
  .rec-card-1 {
    width: 100%;
    margin-bottom: 32px;
  }
}
