.results-image-icon1 {
  position: relative;
  border-radius: 10px 0 0 10px;
  width: 260px;
  height: 220px;
  flex-shrink: 0;
  object-fit: cover;
}
.bed-in-quad,
.freehand-los-angeles {
  position: absolute;
  left: 0;
  display: inline-block;
}
.freehand-los-angeles {
  top: 25px;
  font-size: 22px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  width: 273.87px;
}
.bed-in-quad {
  top: 0;
  letter-spacing: 0.02em;
  color: #8e8e94;
  width: 172.79px;
}
.reviews2 {
  left: 64.07px;
  letter-spacing: 0.04em;
  display: inline-block;
  width: 138.7px;
}
.div4,
.reviews2,
.vector-icon2 {
  position: absolute;
  top: 0;
}
.div4 {
  left: 25.86px;
  letter-spacing: 0.04em;
  font-weight: 500;
  display: inline-block;
  width: 28.21px;
}
.vector-icon2 {
  height: 84.21%;
  width: 34.78%;
  right: 65.22%;
  bottom: 15.79%;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.review1,
.stars1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 54.07px;
  height: 19px;
  color: #303030;
}
.review1 {
  top: 57px;
  width: 202.77px;
  color: #1262af;
}
.result {
  flex: 1;
  position: relative;
  height: 76px;
}
.results-title1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.night2,
.s-198 {
  position: absolute;
  letter-spacing: 0.02em;
}
.s-198 {
  top: 0;
  left: 0;
  text-transform: capitalize;
}
.night2 {
  top: 8px;
  left: 73px;
  font-size: 14px;
}
.pricing1,
.view-details-button-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 112px;
  height: 26px;
}
.view-details-button-item {
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  border-radius: 6px;
  background-color: #1262af;
}
.search-flights2 {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 54.5px);
  font-size: 18px;
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: Roboto;
  color: #fff;
  text-align: center;
}
.view-details-button1 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  top: 37.58px;
  left: 0.49px;
  width: 164px;
  height: 46px;
}
.price-and-cta1 {
  position: relative;
  width: 164.49px;
  height: 83.58px;
  flex-shrink: 0;
  font-size: 22px;
}
.results-card1,
.results-details1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.results-details1 {
  flex: 1;
  flex-direction: column;
  padding: 15px 12px 15px 0;
  gap: 31px;
}
.results-card1 {
  align-self: stretch;
  border-radius: 10px;
  background-color: rgba(18, 98, 175, 0.05);
  flex-direction: row;
  gap: 16px;
  text-align: left;
  font-size: 16px;
  color: #303030;
  font-family: Roboto;
}
@media screen and (max-width: 427px) {
  .results-image-icon1 {
    width: 100% !important;
    border-radius: 10px !important;
  }
  .results-details1 {
    padding: 15px;
    box-sizing: border-box;
    flex: unset;
    align-self: stretch;
  }
  .results-card1 {
    flex-direction: column;
  }
}
