.turkish-icon {
  position: relative;
  width: 34.77px;
  height: 46px;
  flex-shrink: 0;
}
.singapore-airlines {
  position: relative;
  letter-spacing: 0.26px;
}
.airline-section {
  width: 20%;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 7px;
}
.am,
.sin1 {
  position: absolute;
  left: 0;
  display: inline-block;
}
.am {
  width: 100%;
  top: 0;
  letter-spacing: 0.32px;
  font-weight: 500;
}
.sin1 {
  width: 79.51%;
  top: 63.46%;
  font-size: 16px;
  letter-spacing: 0.26px;
  color: #646468;
}
.departure-details1 {
  position: relative;
  width: 93px;
  height: 52px;
  flex-shrink: 0;
}
.flighticon {
  position: absolute;
  height: 27.46%;
  width: 88.52%;
  top: 0;
  right: 5.5%;
  bottom: 72.54%;
  left: 5.98%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.h-55m-non-stop {
  position: absolute;
  top: 65.45%;
  left: 0;
  letter-spacing: 0.21px;
}
.flightduration {
  position: relative;
  width: 121px;
  height: 55px;
  flex-shrink: 0;
  text-align: center;
  font-size: 16px;
  color: #8e8e94;
}
.sin2 {
  position: absolute;
  width: 81.25%;
  top: 63.46%;
  left: 17.65%;
  font-size: 16px;
  letter-spacing: 0.26px;
  color: #646468;
  display: inline-block;
}
.arrival-details {
  position: relative;
  width: 91px;
  height: 52px;
  flex-shrink: 0;
  text-align: right;
}
.flight-details-section {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 64%;
  font-size: 21px;
  color: #1262af;
}
.price-section-child {
  position: relative;
  width: 1px;
  height: 100px;
  flex-shrink: 0;
}
.s-730 {
  flex: 1;
  position: relative;
  letter-spacing: 0.34px;
}
.flight-card,
.price-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.price-section {
  width: 128px;
  flex-shrink: 0;
  text-align: center;
  font-size: 24px;
  color: #fba403;
}
.flight-card {
  align-self: stretch;
  border-radius: 6px;
  background-color: #fff;
  border: 0.7px solid #cecece;
  padding: 0 25px;
  gap: 50px;
  text-align: left;
  font-size: 16px;
  color: #57575d;
  font-family: Roboto;
}
@media screen and (max-width: 767px) {
  .airline-section {
    flex-direction: column;
  }
  .flight-details-section {
    flex-direction: row;
  }
}
@media screen and (max-width: 427px) {
  .turkish-icon {
    transform: scale(0.8);
  }
  .airline-section {
    flex-direction: row;
    width: 100% !important;
    margin-left: 40px;
  }
  .flightduration {
    transform: scale(0.9);
  }
  .flight-details-section {
    width: 90%;
    flex-direction: row;
    padding: 0;
    box-sizing: border-box;
    margin-right: 0 !important;
    flex: unset;
    align-self: stretch;
  }
  .price-section-child {
    display: none;
  }
  .flight-card {
    flex-direction: column;
    padding: 20px 0;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .am,
  .sin1 {
    font-size: 16px;
  }
  .departure-details1 {
    width: 80px;
  }
  .h-55m-non-stop {
    font-size: 13px;
  }
  .sin2 {
    font-size: 16px;
  }
  .arrival-details {
    width: 80px;
  }
  .flight-details-section {
    width: 90%;
  }
}
@media screen and (max-width: 310px) {
  .airline-section {
    flex-direction: row;
    padding-bottom: 10px;
    box-sizing: border-box;
    width: 100%;
    margin-right: 0;
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 16px;
  }
  .flight-details-section {
    width: 100%;
    margin-right: 0;
    flex: unset;
    align-self: stretch;
  }
  .price-section-child {
    display: none;
  }
  .flight-card,
  .price-section {
    box-sizing: border-box;
    width: 100%;
  }
  .price-section {
    padding: 6px;
    background-color: #f5f7f8;
    margin-top: 16px;
  }
  .flight-card {
    display: flex;
    flex-direction: column;
    padding: 25px;
  }
}
