.popular-stays1 {
  position: absolute;
  top: 0;
  left: 0;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  display: inline-block;
  width: 1105.34px;
}
.popular-stays-title {
  flex: 1;
  position: relative;
  height: 35px;
}
.view-all-stays {
  position: relative;
  font-size: 18px;
  letter-spacing: 0.04em;
  font-weight: 500;
  font-family: Roboto;
  color: #1262ae;
  text-align: right;
  display: inline-block;
  width: 140.66px;
  flex-shrink: 0;
}
.arrow-right-icon4 {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.view-all-stays-button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: #fff;
  border-radius: 31px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 10px;
}
.hotel-cards,
.popular-stays-header {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.hotel-cards {
  justify-content: flex-start;
  margin-left: -12px;
  margin-right: -12px;
  font-size: 16px;
}
.view-all-stays1 {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 500;
  display: inline-block;
  width: 140.66px;
  flex-shrink: 0;
}
.mobile-view-all-stays,
.popular-stays {
  align-items: flex-start;
  justify-content: flex-start;
}
.mobile-view-all-stays {
  border-radius: 31px;
  background-color: #fff;
  display: none;
  flex-direction: row;
  gap: 10px;
  text-align: right;
  font-size: 18px;
  color: #1262ae;
}
.popular-stays {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: left;
  font-size: 30px;
  color: #303030;
  font-family: Roboto;
}
@media screen and (max-width: 1440px) {
  .hotel-cards {
    padding-right: 40px;
    box-sizing: border-box;
    margin-left: -12px;
    margin-right: -50px;
  }
}
@media screen and (max-width: 767px) {
  .view-all-stays-button {
    display: none;
  }
  .hotel-cards {
    margin-right: -50px;
    margin-left: -12px;
  }
  .view-all-stays1 {
    width: auto;
  }
  .mobile-view-all-stays {
    display: flex;
    margin-top: 16px;
  }
}
@media screen and (max-width: 427px) {
  .popular-stays1 {
    font-size: 22px;
  }
  .hotel-cards {
    flex-direction: column;
    padding-right: 0;
    box-sizing: border-box;
    margin-right: -12px;
    margin-left: -12px;
  }
}
